/* UI template styles */
.ui {
  font-family: 'Roboto', sans-serif;
  background: #fcfcfc;
  padding: 0 30px;
}
.ui .expansion-panel {
  -webkit-box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
  box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
  border-radius: 5px;
}
.ui section {
  max-width: 980px;
  padding: 20px 0;
}
.ui section > h2 {
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  margin-bottom: 10px;
}
.ui section .row .col {
  font-size: 18px;
  padding: 15px;
  color: #fff;
