body {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 1.6;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  font-family: 'Open Sans Condensed', sans-serif;
  font-weight: 700;
}
h1 {
  font-size: 36px;
}
h2 {
  font-size: 30px;
}
h3 {
  font-size: 24px;
}
h4 {
  font-size: 18px;
}
h5 {
  font-size: 14px;
}
h6 {
  font-size: 12px;
}
a[href^="tel"] {
  cursor: default;
}
p {
  margin-top: 0;
  margin-bottom: 20px;
}
article {
  position: relative;
  color: #4a454b;
}
article a {
  text-decoration: underline;
}
article a:hover {
  color: #f00;
}
article img {
  max-width: 100%;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  article img {
    width: auto;
    float: left;
    max-width: 100%;
    margin-right: 25px;
    margin-bottom: 10px;
  }
}
article h1,
article h2,
article h3,
article h4 {
  margin-bottom: 0.5em;
  color: #4a454b;
}
article h2 {
  font-size: 24px;
}
article h3 {
  font-size: 20px;
}
article h4 {
  font-size: 16px;
}
article p:last-child {
  margin-bottom: 0;
}
article blockquote {
  border-left: 3px solid #424242;
  padding-left: 20px;
}
article > ul {
  padding-bottom: 20px;
}
article > ul li {
  position: relative;
  padding-left: 10px;
}
article > ul li:before {
  content: '';
  color: #424242;
  font-weight: bold;
  position: absolute;
  left: 0;
  top: 0;
}
article > ul li + li {
  margin-top: 5px;
}
article ol li {
  position: relative;
  padding-left: 30px;
  counter-increment: step-counter;
  margin-bottom: 15px;
}
article ol li:before {
  content: counter(step-counter);
  margin-right: 10px;
  margin-bottom: 4px;
  font-size: 80%;
  background-color: #424242;
  color: #fff;
  font-weight: bold;
  padding: 3px 8px;
  border-radius: 2px;
  position: absolute;
  left: 0;
  top: 0;
}
article .lead-paragraph {
  background: #ccaed3;
  color: #fff;
  padding: 15px;
  margin-bottom: 20px;
}
article .lead-paragraph p:last-child {
  margin-bottom: 0;
}
article .backward {
  padding-top: 20px;
  border-top: 1px solid #7b5481;
}
article .backward a {
  -webkit-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  -o-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  background: #94b371;
  border-radius: 5px;
  color: #f6f0f6;
  text-decoration: none;
  padding: 10px 30px;
  position: relative;
  display: inline-block;
  margin-left: 10px;
}
article .backward a:after {
  -webkit-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  -o-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 10px 5px 0;
  border-color: transparent #94b371 transparent transparent;
  position: absolute;
  left: -10px;
  top: 50%;
  margin-top: -5px;
}
article .backward a:hover {
  background: #7b5481;
}
article .backward a:hover:after {
  border-color: transparent #7b5481 transparent transparent;
}
.page-title {
  position: relative;
  margin-bottom: 30px;
}
.page-title .m_pagination {
  position: absolute;
  right: 0;
  top: -2px;
  display: none;
}
@media only screen and (min-width: 768px) {
  .page-title .m_pagination {
    display: block;
  }
}
.page-title .counter {
  position: absolute;
  right: 0;
  top: 0;
  color: #4a454b;
  line-height: 28px;
}
.page-title h2 {
  font-size: 24px;
  padding-left: 40px;
  position: relative;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  padding-right: 65px;
}
.page-title h2:after {
  content: '';
  background: url("../images/sprite.png") no-repeat -150px 0;
  width: 31px;
  height: 27px;
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -13.5px;
}
/* Components styles */
.btn {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
  vertical-align: middle;
  background: #e0e0e0;
  border-radius: 2px;
  font-size: 14px;
  font-weight: 500;
  min-width: 88px;
  margin: 6px 5px 6px 0;
  padding: 10px 16px;
  text-transform: uppercase;
  text-decoration: none;
  will-change: box-shadow;
